*,
*::before,
*::after   {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

a {
    text-decoration: none;
    color: inherit;
}

ul,
ol,
li {
	list-style: none;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
    font-weight: inherit;
	  font-size: inherit;
}

img {
  vertical-align: top;
}
